<template>
    <div class="view-wrapper" :style="{minHeight:pageMixin_browser.clientHeight}">
        <div class="top">
            <div class="logo">
            </div>
            <div class="vip">
                <img :src="pageParameter.newComerVipBackground" alt="">
            </div>
        </div>

        <div class="form">
            <p class="label">
                仅好友邀请可得VIP会员
            </p>
            <div v-if="success">

                <DP_Button background-color="#8e370f">你已经领取VIP</DP_Button>
                <p class="account">
                    账号：{{phone}}
                </p>

                <div class="app">请到：熊猫博士识字>登陆>用户中心 查看</div>

            </div>
            <div v-else>
                <input class="phone" v-model.trim="phone" maxlength="11" type="text" placeholder="请输入你的手机号"/>
                <!--               <button @click="getVip">免费领取</button>-->
                <DP_Button @click.native="getVip">免费领取</DP_Button>
            </div>

        </div>

        <div v-if="!success" class="introduce">
            <div class="part1">
                <div class="title">
                </div>
                <div class="content">
                    <img src="../../../assets/h5/invitee/bz2.png" alt="">
                </div>
            </div>
            <div class="part2">
                <div class="title">
                    课程亮点
                </div>
                <div class="content">
                    <img src="../../../assets/h5/invitee/l1.png" alt="">
                    <img src="../../../assets/h5/invitee/l2.png" alt="">
                    <img src="../../../assets/h5/invitee/l3.png" alt="">
                    <img src="../../../assets/h5/invitee/l4.png" alt="">
                    <img src="../../../assets/h5/invitee/l5.png" alt="">
                    <img src="../../../assets/h5/invitee/l6.png" alt="">
                    <img src="../../../assets/h5/invitee/l7.png" alt="">
                </div>
            </div>
        </div>


        <div class="download" @click="down">
            <p>
                下载“熊猫博士识字”
                <br>
                登录即可畅玩

            </p>
            <div class="down">

            </div>
        </div>

        <!--        <button @click="dp_jump('error',{tips:'系统出错了'})">测试跳转</button>-->

        <!--        <button @click="test">网络请求</button>-->

    </div>
</template>

<script>
    import {getNew2} from '@/api/test';
    import DP_Button from '../../../components/DP_Button'
    import {mapGetters} from "vuex";
    import {getReward} from "@/api/parameter";
    import {getQueryString} from "@/utils/function";
    import {checkPhone} from "@/utils/function";
    import {sernsorFn} from "@/utils/sensors";
    import pageMixin from "@/mixin/pageMixin";
    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "index",
        props: {},
        components: {
            DP_Button,
        },
        mixins: [pageMixin],
        data() {
            return {
                phone: "",
                success: false,
                tips: {
                    show: false,
                    title: "提示",
                    content: ""
                }
            }
        },
        watch: {},
        computed: {
            ...mapGetters({
                pageParameter: "getPageParameter",
            }),
        },
        methods: {
            down() {
                sernsorFn('buttonClick', {
                    tabTitle: document.title,
                    moduleTitle: 'Referral',
                    buttonType: "banner",
                    buttonName: "立即下载"
                });
                const type = getQueryString('type');
                let downLink = 'http://sensorsdata-2.talbrain.com:8106/r/fd'
                //type=0就是免费用户，1就是付费
                if (type == 0) {
                    //免费用户
                    downLink = 'http://sensorsdata-2.talbrain.com:8106/r/fd'
                } else if (type == 1) {
                    downLink = 'http://sensorsdata-2.talbrain.com:8106/r/Jd'
                }
                window.location.href = downLink;
            },
            test() {
                getNew2();
            },
            // showPopup(title, content) {
            //     this.tips.title = title;
            //     this.tips.content = content;
            //     this.tips.show = true;
            // },
            // closePopup() {
            //     this.tips.show = false;
            // },
            getVip() {
                sernsorFn('buttonClick', {
                    tabTitle: document.title,
                    moduleTitle: 'Referral',
                    buttonType: "普通按钮",
                    buttonName: "领取VIP"
                });

                if (checkPhone(this.phone)) {
                    const refAccountId = getQueryString('ref-accountId');
                    if (refAccountId) {
                        // eslint-disable-next-line no-undef
                        // eslint-disable-next-line no-undef
                        getReward({
                            refAccountId,
                            phoneNumber: this.phone,
                            bundleId: "com.drpanda.chineseacademy",
                            tag: "referralV2.0",
                        }).then(res => {
                            const {data: {code, data}} = res;
                            if (code === 0) {
                                this.success = true;
                                if (data.isNewUser) {
                                    // eslint-disable-next-line no-undef
                                    sernsorFn('signUp');
                                }else {
                                    // eslint-disable-next-line no-undef
                                    // sernsorlogin("+86" + this.phone)
                                    sernsorFn('login');
                                }
                            }
                            // else {
                            //     this.showPopup('提示',message);
                            // }
                        });

                    } else {
                        // this.showPopup('提示', '参数错误');
                        this.dp_Tips('提示', '参数错误');

                    }


                } else {
                    // this.showPopup('提示', '手机号码错误');
                    this.dp_Tips('提示', '手机号码错误');
                }
            }
        },
        created() {

        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
    .view-wrapper {
        position: relative;
        width: 100%;
        padding-bottom: 200px;
        overflow: hidden;

        .top {
            box-sizing: border-box;
            width: 100%;
            height: 1134px;
            background-color: #ff4438;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            position: relative;
            background-image: url("../../../assets/h5/invitee/Bitmap.jpg");

            .logo {
                width: 264px;
                height: 189px;
                background-image: url("../../../assets/h5/invitee/Group83.png");
                background-size: 100% 100%;
                position: absolute;
                top: 50px;
                left: 50%;
                margin-left: -132px;
            }

            .vip {
                width: 737px;
                height: 309px;

                img {
                    width: 100%;
                    height: 100%;
                }

                /*background-image: url("../../../assets/h5/invitee/3vip.png");*/
                background-size: 100% 100%;
                position: absolute;
                top: 274px;
                left: 50%;
                margin-left: -368.5px;

            }
        }

        .form {
            padding: 138px 0;
