            box-sizing: border-box;

            .label {
                font-size: 66px;
                line-height: 70px;
                margin-bottom: 66px;
                font-weight: bold;
            }

            .account {
                color: #54c0e8;
                font-size: 48px;
                margin-top: 80px;
            }

            .app {
                text-align: center;
                font-size: 48px;
                color: #333333;
                margin: 50px auto;
            }

            .phone {
                width: 942px;
                height: 180px;
                background-color: #c4eaff;
                border: none;
                border-radius: 20px;
                text-align: center;
                font-size: 66px;
                margin-bottom: 66px;
            }

            button {
                width: 570px;
                height: 150px;
                background-color: #0082ca;
                border: none;
                border-radius: 20px;
                color: white;
                font-size: 40px;
                font-weight: bold;
            }
        }

        .introduce {
            background-color: #fffdea;
            padding: 66px 0;
            text-align: center;
            box-sizing: border-box;

            .part1 {
                background-image: url("../../../assets/h5/invitee/tt.png");
                background-position: left bottom;
                background-repeat: no-repeat;
                background-size: 600px 204px;
                padding-bottom: 240px;
                margin-bottom: 20px;

                .title {
                    background-image: url("../../../assets/h5/invitee/Group37.png");
                    width: 411px;
                    height: 165px;
                    background-size: 100% 100%;
                    margin: 0 auto;
                    margin-bottom: 84px;
                }

                .content {

                    img {
                        width: 942px;
                    }

                }
            }

            .part2 {
                .title {
                    font-size: 90px;
                    font-weight: bold;
                    color: #0f3b53;
                    margin-bottom: 58px;
                }

                .content {
                    img {
                        width: 1014px;
                        margin-bottom: 90px;
                    }
                }

            }

        }

        .download {
            background-color: #54c0e8;
            color: white;
            padding: 70px;
            text-align: center;
            position: fixed;
            bottom: 0;
            left: 0;
            width: 100%;
            box-sizing: border-box;
            opacity: .95;

            p {
                font-size: 60px;
            }

            .down {
                width: 118px;
                height: 118px;
                background-image: url("../../../assets/h5/invitee/down.png");
                background-size: 100% 100%;
                position: absolute;
                right: 50px;
                top: 50%;
                margin-top: -59px;
                animation: bounceIn 1s linear infinite alternate;
            }


        }
    }

    /*.popup {*/
